import React from "react";
import {
  BrowserRouter as Router,
  Switch as RouterSwitch,
  Route
} from "react-router-dom";
import CssBaseline from "@material-ui/core/CssBaseline";
import SlideShowPage from "./components/SlideShow";
import MainPage from "./components/MainPage";
import ConfigFormPage from "./components/ConfigForm";
import DataTable from "./components/DataTable";
import ConfigMenuPage from "./components/ConfigMenu";
import ConfigMenuPage2 from "./components/ConfigMenu2";
import DataTableRestApi from "./components/DataTableRestApi";
import FormSteps from "./components/FormSteps";
import WikiUp from "./components/WikiUp";
import WikiUpPage from "./components/WikiUpPage";
import VideoBg from "./components/VideoBg";
import { ThemeProvider as MuiThemeProvider } from "@material-ui/core/styles";
import SwitchComponent from "./components/SwitchComponent";
import ArticleList from "./components/ArticleList";
import { AppStateContext } from "./components/AppState";
import DateRangeCalendar from "./components/DateRangeCal";

function Main() {
  const store = React.useContext(AppStateContext);

  const [isOpen, setIsOpen] = React.useState();

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  return (
    <MuiThemeProvider theme={store.currentTheme}>
      <SwitchComponent />
      <CssBaseline />
      <Router>
        <RouterSwitch>
          <Route path="/slideshow">
            <SlideShowPage />
          </Route>
          <Route path="/configform">
            <ConfigFormPage />
          </Route>
          <Route path="/configmenu">
            <ConfigMenuPage />
          </Route>
          <Route path="/test">
            <ConfigMenuPage2 />
          </Route>
          <Route path="/datatable">
            <DataTable />
          </Route>
          <Route path="/datatable-rest-api">
            <DataTableRestApi />
          </Route>
          <Route path="/forms-teps">
            <FormSteps />
          </Route>
          <Route path="/wiki">
            <WikiUp />
          </Route>
          <Route path="/manual">
            <WikiUpPage />
          </Route>
          <Route path="/videoapp">
            <VideoBg />
          </Route>
          <Route path="/articleList">
            <ArticleList />
          </Route>
          <Route path="/calendar">
            <DateRangeCalendar />
          </Route>
          <Route
            path="/bio"
            render={() => {
              return <MainPage toggle={toggle} isOpen={false} />;
            }}
          ></Route>
          <Route
            path="/"
            render={() => {
              return <MainPage toggle={toggle} isOpen={true} />;
            }}
          ></Route>
        </RouterSwitch>
      </Router>
    </MuiThemeProvider>
  );
}

export default Main;
