import { makeStyles } from "@material-ui/core/styles";
import image from "../assets/image.jpeg";
import blurImg from "../assets/blurImg.jpg";
import { useState, useEffect, useRef } from "react";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "row",
    [theme.breakpoints.down("sm")]: {
      flexDirection: "column"
    }
  },
  image: {
    opacity: 0.9,
    width: 335,
    height: 256,
    margin: "0 30px 30px 0",
    [theme.breakpoints.down("sm")]: {
      backgroundSize: "cover",
      backgroundPosition: "center center"
    }
  },
  blurImg: {
    position: "absolute",
    opacity: 0.6,
    width: 335,
    backgroundRepeat: "no-repeat",
    backgroundPosition: "top right",
    height: 256,
    filter: "blur(3px)",
    transition: "opacity ease-in 1000ms",
    backgroundImage: `url(${blurImg})`,
    margin: "0 30px 30px 0",
    [theme.breakpoints.down("sm")]: {
      backgroundSize: "cover",
      backgroundPosition: "center center"
    }
  },
  description: {
    width: 600,
    "@media (min-width:730px)": {
      width: 300
    },
    "@media (min-width:1228px)": {
      width: 600
    },
    [theme.breakpoints.down("sm")]: {
      width: "calc(100% - 10px)",
      padding: 15
    }
  }
}));

const useImageLoaded = () => {
  const [loaded, setLoaded] = useState(false);
  const ref = useRef();

  const onLoad = () => {
    setLoaded(true);
  };

  useEffect(() => {
    if (ref.current && ref.current.complete) {
      onLoad();
    }
  });

  return [ref, loaded, onLoad];
};

export default function BioComponent() {
  const classes = useStyles();

  const [ref, loaded, onLoad] = useImageLoaded();

  return (
    <div className={classes.root}>
      <div className={classes.image}>
        <div
          className={classes.blurImg}
          {...(loaded && { style: { opacity: "0" } })}
        />
        <img onLoad={onLoad} className={classes.image} src={image} ref={ref} />
      </div>

      <div className={classes.description}>
        <p>
          I graduated from Saint Petersburg State University in 2008 with a B.A.
          in Information Technology. The program splits strong skills in
          Information technology and Design thinking.
        </p>
        <p>
          Right After my graduation, I was lucky to join an international team
          working on the book Encyclopedia of Russian Avangard. I was
          responsible for the WEB version of the book. That was the first
          scalable project that gave me a unique experience in software
          development.
        </p>
        <p>
          Today, my focus covers responsible for Developing the User Interface:
        </p>
        <ul>
          <li>Designing User Interface Elements</li>
          <li>Loading and Displaying Content across various devices</li>
          <li> Handling User Interaction</li>
          <li> Input and Submiting data to server side</li>
          <li> Network Requests</li>
        </ul>
        <p>
          For the last few years, I responsible for the front-end part of the
          development of affiliate software. My workflow includes translating
          UX/UI wireframes to interactive components into further implementation
          with the back-end part.
        </p>
      </div>
    </div>
  );
}
