import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useSpring, animated } from "react-spring";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Container from "@material-ui/core/Container";
import Card from "./card";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: theme.spacing(10),
    padding: 0,
    [theme.breakpoints.up("lg")]: {
      padding: theme.spacing(0, 5, 20, 9)
    }
  },
  title: {
    fontFamily: "Big Shoulders Stencil Text",
    fontWeight: 100,
    fontSize: 140,
    color: "#f92f6c",
    lineHeight: 0.9,
    [theme.breakpoints.down("sm")]: {
      fontSize: 100
    }
  }
}));

const calc = (x, y) => [x - window.innerWidth / 2, y - window.innerHeight / 2];
const trans = (x, y) => `translate3d(${x / 50}px,${y / 15}px,0)`;

function MainPage({ isOpen, toggle, isBio }) {
  const classes = useStyles();

  const [pos, set] = useSpring(() => ({
    xy: [0, 0],
    config: { mass: 10, tension: 550, friction: 140 }
  }));

  const handleMouseMove = ({ clientX: x, clientY: y }) =>
    set({ xy: calc(x, y) });

  return (
    <Container maxWidth="xl" disableGutters={true} className={classes.root}>
      <div className={classes.root} onMouseMove={handleMouseMove}>
        <animated.h1
          className={classes.title}
          style={{ transform: pos.xy.interpolate(trans) }}
        >
          Irina <br /> Karpova
        </animated.h1>
        <Box
          ml={16.5}
          mt={3}
          display="flex"
          flexDirection="row"
          alignItems="flex-end"
        >
          <Typography variant="h6">
            Front End Development. <br />
            UI design
          </Typography>
        </Box>

        <Card isOpen={isOpen} toggle={toggle} />
      </div>
    </Container>
  );
}

export default MainPage;
