import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useSpring, animated } from "react-spring";
import { Link, Route } from "react-router-dom";
import Container from "@material-ui/core/Container";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import data from "../data";
import Footer from "./Footer";
import CssBaseline from "@material-ui/core/CssBaseline";
import BioComponent from "./BioComponent";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    "& > *": {
      margin: theme.spacing(0, 1, 1, 0)
    },
    marginTop: theme.spacing(15),
    [theme.breakpoints.down("sm")]: {
      "& > *": {
        margin: theme.spacing(0, 0, 1, 0)
      }
    }
  },
  titles: {
    zIndex: 100,
    whiteSpace: "pre",
    marginTop: "auto",
    marginBottom: "auto",
    paddingRight: 24
  },
  imageContainer: {
    display: "flex",
    position: "absolute",
    zIndex: 10,
    width: 335,
    backgroundRepeat: "no-repeat",
    backgroundPosition: "top right",
    backgroundSize: "contain",
    minHeight: 256
  },
  bgContainer: {
    zIndex: 0,
    position: "absolute",
    minHeight: 256
  },

  rootBoxes: {
    alignItems: "flex-end",
    justifyContent: "flex-end",
    height: theme.spacing(32),
    width: 340
  },

  a_link: {
    textDecoration: "none",
    "&:hover, &:visited": {
      textDecoration: "underline"
    }
  },
  buttonOnBio: {
    cursor: "pointer",
    marginLeft: 320,
    marginTop: 30,
    "@media (max-width:320px )": {
      marginLeft: 280
    },
    "@media (min-width:712px )": {
      marginLeft: 665
    },
    "@media (min-width:1268px)": {
      marginLeft: 1030
    }
  }
}));

export default function Card({ isOpen, toggle }) {
  const classes = useStyles();

  const animationFlow = useSpring({
    to: {
      width: isOpen ? 340 : 8,
      opacity: isOpen ? 1 : 0
    },

    from: {
      width: 335,
      opacity: 1
    }
  });

  return (
    <Container disableGutters={true} maxWidth="xl">
      <CssBaseline />

      <Box onClick={toggle} flexGrow={1} className={classes.buttonOnBio}>
        {isOpen ? (
          <Link to="/bio">
            <Typography align="left" variant="h6">
              Bio
            </Typography>
          </Link>
        ) : (
          <Link to="/">
            <Typography align="left" variant="h6">
              X
            </Typography>
          </Link>
        )}
      </Box>

      <div className={classes.root}>
        <Route path="/bio">
          <div
            style={{
              position: "absolute"
            }}
          >
            <BioComponent isOpen={isOpen} />
          </div>
        </Route>

        {data.map(({ bgcolor, color, image, href, text, id }, index) => {
          return (
            <div
              style={
                !isOpen && index !== 0
                  ? {
                      display: "none"
                    }
                  : {
                      display: "flex"
                    }
              }
              key={index}
              id={id}
              className={classes.rootBoxes}
            >
              <animated.div
                className={classes.titles}
                style={{ opacity: animationFlow.opacity }}
              >
                <Typography variant="h5" component="p">
                  {isOpen && (
                    <Link
                      className={classes.a_link}
                      to={href}
                      style={{
                        color: color
                      }}
                    >
                      {text}
                    </Link>
                  )}
                </Typography>
              </animated.div>
              <Box
                style={{
                  backgroundImage: isOpen && `url(${image})`
                }}
                className={classes.imageContainer}
              ></Box>

              <animated.div
                style={{
                  backgroundColor: bgcolor,
                  width: index === 0 ? animationFlow.width : 340
                }}
                className={classes.bgContainer}
              ></animated.div>
            </div>
          );
        })}
        <Footer />
      </div>
    </Container>
  );
}
